import React, { useState, useEffect } from 'react'
import { makeStyles, Card, CardContent, Typography, CircularProgress } from '@material-ui/core'
import { useSelector, useDispatch } from 'react-redux'
import {appService} from '../../../app.service'
import io from "socket.io-client";
const useStyle = makeStyles((theme)=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function Location() {
    const classes = useStyle()
    const patient = useSelector(state => state.patientReducer)
    const dispatch = useDispatch()
    const [loading, setLoading] = useState(true)
    const [currentLocation,setCurrentLocation]  = useState('');
    useEffect(()=>{
        if(patient.hubID){
            setLoading(true)
            appService.currentLocation(patient.hubID).then(json=>{
                if(json.success){                    
                    // //console.log("formatted data",json.message)                    
                    setCurrentLocation(json.message.area)
                    setLoading(false)
                }
            })
        }
    },[])
    const socket = io('https://oncierreapi.azurewebsites.net');

    const [, updateState] = React.useState();
    const forceUpdate = React.useCallback(() => updateState({}), []);

    const [sensorStatus,setSensorStatus] = useState([
        { area : "Door", status : false, imgResourceNo:3 }, 
        { area : "Bed", status : false, imgResourceNo:2}, 
        { area : "Kitchen", status : false, imgResourceNo:1 }, 
        { area : "Bedroom", status : false, imgResourceNo:1 },
        { area : "Bathroom", status : false, imgResourceNo:1 }, 
    ])

    const [lastContactTs,setLastContactTs] = useState(new Date())

    const [sensors, setSensors] = useState([])

    // const newSensor = (area,status) => {
  
    // }

    // const [doorSensorStatus,setDoorSensorStatus] = useState("Fetching");
    const [systemStatus,setSystemStatus] = useState("Fetching");
    
    let offlineCheckTimer;
        
    const setTimer = () => {
        // console.log("Timer set")
        offlineCheckTimer = setTimeout(() => {
            if(systemStatus === 'Fetching' || systemStatus === 'Online'){
                setSystemStatus("Offline")
            }
        }, 60000);
    }

    // const getLastContact = () => {

    // }

    useEffect(() => {
        appService.lastContact(patient.hubID)
        .then(resp =>{
            let d = new Date(0)
            d.setUTCSeconds(resp[0].ts)
            setLastContactTs(d)
            // console.log(d.toString())
        },
        err=> console.log("Resp1:",err))
        appService.currentLocation(patient.hubID)
        .then(resp =>{
            console.log("Location is",resp)
        },
        err=> console.log("Resp1:",err))

        setTimer()
        console.log("Listner created")
        if(patient.hubID){
            appService.statusMessagesSubscribe(patient.hubID)
            .then(json => {
                if(json==="logout"){
                    console.log("Forbidden Access - Logging out")
                    dispatch({type:'logout'})
                }
                else{

                        console.log("Subscribed to : ",json.message.room)
                        let tempObj = sensorStatus

                        socket.on(json.message.room, (message) => {
                        console.log("message recived",message)
                            if(message.message){
                                console.log("Recived heartbeat")

                            }else{
                                if(message.ts){
                                    let d = new Date(0)
                                    d.setUTCSeconds(message.ts)
                                    setLastContactTs(d)
                                }
                                if(message.area === "door"){
                                    
                                    if(message.status === "close"){
                                        tempObj.map(item => {
                                            if(item.area === "Door"){
                                                item.status = false
                                            }
                                        })
                                    }else{
                                        tempObj.map(item => {
                                            if(item.area === "Door"){
                                                item.status = true
                                            }
                                        })
                                    }
                                }else
                                if(message.area === "bed"){
                                    
                                    if(message.status === "on-bed"){
                                        tempObj.map(item => {
                                            if(item.area === "Bed"){
                                                item.status = true
                                            }
                                        })
                                    }else{
                                        tempObj.map(item => {
                                            if(item.area === "Bed"){
                                                item.status = false
                                            }
                                        })
                                    }
                                }else
                                if( message.area === "kitchen" ){
                                    
                                    tempObj.map(item => {
                                        if(item.area === "Kitchen"){
                                            item.status = message.motion 
                                        }
                                    })
                                }else    
                                if( message.area === "bedroom" ){
                                    tempObj.map(item => {
                                        if(item.area === "Bedroom"){
                                            item.status = message.motion 
                                        }
                                    })
                                }else
                                if(message.area === "bathroom"){
                                    tempObj.map(item => {
                                        if(item.area === "Bathroom"){
                                            item.status = message.motion 
                                        }
                                    })
                                }
                                else{
                                    console.log("Unknown area ",message.area)
                                }
 
                            }
                            setSystemStatus("Online")
                            console.log("setting with ",tempObj)
                            setSensorStatus(tempObj)
                            forceUpdate()
                            clearTimeout(offlineCheckTimer)
                            setTimer()
                        })
                    }
            }, error => {
            console.log("error",error)
            })

        }else{
            console.log("No HUB ID found for selected user")
        }



        return function cleanup() {
            clearTimeout(offlineCheckTimer)
            if(patient.hubID){
                appService.statusMessagesUnSubscribe(patient.hubID)
                .then(json => {
                    if(json==="logout"){
                        console.log("Forbidden Access - Logging out")
                        dispatch({type:'logout'})
                    }else{
                        
                        console.log("Unsubscribed",json.message.room)
                        // socket.emit("room",json.message.room)
                    }}, error => {
                        console.log("Unsubscrib Error",error)
                    })
            }else{
                console.log("No HUB ID found for selected user")
            }
            

            socket.disconnect()
        }
      
    },[])
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    {loading?(<CircularProgress/>):(
                        <div>
                            <Typography className={classes.info} variant="h6">Location</Typography>
                            <Typography className={classes.info}  variant="h5">{currentLocation}</Typography>
                        </div>
                    )}
                </CardContent>
            </Card>
        </div>
    )
}