import React, { useState, useEffect } from 'react'
import { Card, CardContent, Typography, makeStyles, CircularProgress } from '@material-ui/core'
import { useSelector } from 'react-redux'
import {appService} from '../../../app.service'
const useStyle = makeStyles(theme=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function SleepTime() {
    const classes = useStyle()
    const patient = useSelector(state => state.patientReducer)
    const [averageSleep,setAverageSleep]  = useState(0);
    const [loading, setLoading] = useState(true)
    useEffect(()=>{
        setLoading(true)
        if(patient.hubID){
            // const now = 
            appService.fetchSleepData(patient.hubID).then(json=>{
                if(json.success){
                    //console.log(json)
                    json.message = json.message.sort((a,b) => (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0)); 
                    //console.log("formatted sleep data",json.message)
                    var sleep = json.message.map(item => parseInt(item.duration))
                    sleep =sleep.filter(e=>(e!==0))
                    //console.log(sleep)
                    setAverageSleep(sleep.pop())
                    setLoading(false)
                }
            })
        }
    },[])    
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    {loading?(<CircularProgress/>):(
                        <div>
                            <Typography className={classes.info} variant="h6">Sleep Time</Typography>
                            <Typography className={classes.info} variant="h5">{(averageSleep)===null ||averageSleep===undefined?'Data not found': (averageSleep)+' H'}</Typography>
                        </div>
                    )}
                </CardContent>
            </Card>                    
        </div>
    )
}
