import React,{useState} from 'react';
import { 
  Typography,
  Button, 
  TextField,
  Checkbox, 
  Fade, 
  Box,
  IconButton,
  Card,
  Grid,
} from '@material-ui/core';
import { makeStyles } from "@material-ui/core/styles";
import { Mail, Lock,Eye,EyeOff} from 'react-feather';
import InputAdornment from "@material-ui/core/InputAdornment";
import Logo from '../Images/Logo.png'

import {Formik , Field} from 'formik';
import { connect } from "react-redux";
import * as Yup from 'yup';

import {appService} from '../app.service'
import { useEffect } from 'react';


const useStyles = makeStyles((theme) => ({
    root: {
      background:'linear-gradient(-5deg, #172b4d 59.8%, #ffffff 60%)',
      display: 'flex',
      height: '100vh',
      width:'100%',
      flexDirection: 'column'
    },
    container: {
      height:'100vh'
      
    },
    formCard:{
      width:'25%',
      paddingTop:'30px',
      paddingLeft:'40px',
      paddingRight:'40px',
      paddingBottom:'30px',
      marginLeft:'auto',
      marginRight:'auto',
      // marginTop:'10%',
      // marginBottom:'0%',
      textAlign:'center',

      [theme.breakpoints.down('sm')]:{
        width:'85%',
        paddingLeft:'10px',
        paddingRight:'10px',
        marginLeft:'auto',
        marginRight:'auto',
        marginTop:'5%',
      }
    },
    rememberMeCheck:{
      padding: 0,
      marginRight: 10
    },
    alternateOptionText:{
      color:'#FFF',
      paddingTop:'7px',
      fontSize:'15px'
    },
    alternateOptionButton:{

      color:'#11cdef',
      textTransform:'none'
    },
    textfieldResize:{
      fontSize:'16px'
    },
    errorAfterApiCall:{
      paddingTop:'2px',
      color:'red'
    }

}));

const LoginForm = (props) => {
  // console.log("Form Props",props)
    const classes = useStyles();
    const formLocations = {
      login: "LOGIN",
      forgotPass: "FORGOT_PASSWORD",
      changePass: "CHANGE_PASSWORD"
    }
    const [formLocation,setFormLocation] = useState(formLocations.login)

    const [showPassword,setShowPassword] = useState(false)
    const [showPassword1,setShowPassword1] = useState(false)
    const [showPassword2,setShowPassword2] = useState(false)

    const defaultEmail = localStorage.getItem('oncierre-user-email')
    const defaultPassword = localStorage.getItem('oncierre-user-password')
    
    const [rememberMe,setRememberMe] = useState(localStorage.getItem('oncierre-user-email')? true:false)
    const [forgotPassEmail,setForgotPassEmail] = useState('')

    const [resendCodeClicked,setResendCodeClicked] = useState(false)
    const [invalidOtp,setInvalidOtp] = useState(false)
    
    const [expiredOtp,setExpiredOtp] = useState(false)
    let offlineCheckTimer;

    const setExpiredOtpTimer = () => {
      offlineCheckTimer = setTimeout(() => {
        setExpiredOtp(true)
      }, 840000);
    }

    const handleClickShowPassword = () => {
      if(showPassword)
        setShowPassword(false)
      else
        setShowPassword(true)
    };
    const handleClickShowPassword1 = () => {
      if(showPassword1)
        setShowPassword1(false)
      else
        setShowPassword1(true)
    };
    const handleClickShowPassword2 = () => {
      if(showPassword2)
        setShowPassword2(false)
      else
        setShowPassword2(true)
    };
  
    const handleMouseDownPassword = (event) => {
      event.preventDefault();
    };

    const handleRememberMe = () => {
      if(rememberMe)
        {
          setRememberMe(false)
          localStorage.removeItem('oncierre-user-email')
          localStorage.removeItem('oncierre-user-password')
        }
      else
        setRememberMe(true)
    }

    const removeLoginFailure = () => {
      if(props.loginReducer.loginFailure){
        props.removeLoginFailure()
      }
    }
    const removeForgotPassEmailError = () => {
      if(props.loginReducer.forgotPassEmailError){
        props.removeForgotPassEmailError()
      }
    }

    const resendCode = () => {
      setResendCodeClicked(true)
      props.forgotPass(forgotPassEmail)
      setExpiredOtp(false)
      setExpiredOtpTimer()
      alert("New Verification code has been sent to your registered Email Id.")
    }

    const cancelOperation =() => {
      props.removeForgotPassToken()
      setFormLocation(formLocations.login)
    }

    const mtValue=3;

    useEffect(()=> {
      if(props.loginReducer.forgotPassToken !== ''){
        setFormLocation(formLocations.changePass)
      }
      else
      if(props.loginReducer.forgotPassToken === '' && formLocation === formLocations.changePass && !resendCodeClicked){
        setFormLocation(formLocations.login)
      }
    })

    return ( 
      <div className={classes.root}>
        <Grid container className={classes.container} alignContent="center" justify="center">
          {formLocation === formLocations.login &&
            <Fade in={true}>
              <Grid item xs={12} sm={12} md={12} lg={12} >    
                <Card className={classes.formCard} raised={true} onClick={removeLoginFailure}>

                  <img src={Logo}></img>
                  <Typography style={{fontSize:'15px',fontWeight:600}}>Care Solution for Care Providers</Typography>

                  <Formik
                    initialValues={{email:defaultEmail,password:defaultPassword}}
                    validationSchema={Yup.object().shape({
                      email: Yup.string().email("Enter valid Email ID.").max(255).required("Email is required.").nullable(),
                      password: Yup.string().max(255).required('Password is required.').nullable()
                    })}
                    onSubmit={(data,{setSubmitting}) => {
                      setSubmitting(true)
                      props.login(data.email,data.password,rememberMe)
                      setSubmitting(false)

                    }}
                    >
                      {({values,isSubmitting, handleSubmit, handleChange, handleBlur,touched,errors}) => (
                        <form
                           onSubmit={handleSubmit}>


                            <Box mt={mtValue}>
                            <TextField 
                              name="email" 
                              type="input" 
                              placeholder="Email ID" 
                              variant="outlined" 
                              error={Boolean(touched.email && errors.email)}
                              onChange={handleChange}
                              onBlur={handleBlur}
                              style={{fontSize:'16px'}}
                              //autoFocus
                              helperText={touched.email && errors.email}
                              InputProps={{
                                classes: {
                                  input: classes.textfieldResize,
                                },  
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <Mail size="20"/>
                                  </InputAdornment>
                                ),
                              }}
                              fullWidth
                              />
                            </Box>

                            <Box mt={mtValue}>
                              <TextField 
                                name="password" 
                                type={showPassword ? 'text' : 'password'}
                                placeholder="Enter Password" 
                                variant="outlined" 
                                error={Boolean(touched.password && errors.password)}
                                helperText={touched.password && errors.password}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                InputProps={{
                                  classes: {
                                    input: classes.textfieldResize,
                                  },                                  
                                  startAdornment: (
                                    <InputAdornment position="start">
                                      <Lock size="20"/>
                                    </InputAdornment>
                                  ),
                                  endAdornment: (
                                    <InputAdornment position="start">
                                      <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                      >
                                        {showPassword ? <EyeOff /> : <Eye />}
                                      </IconButton>
                                    </InputAdornment>
                                  ),
                                }}
                                
                                fullWidth
                                />
                          </Box>
                          {props.loginReducer.loginFailure &&
                            <Typography className={classes.errorAfterApiCall}>{ props.loginReducer.loginStatusCode === 401 ? "Please enter a valid password." : "Please enter a valid e-mail id." }</Typography>
                          }
                          <Box mt={mtValue} style={{textAlign:'left'}}>
                            <Grid container justify="space-between" fullWidth>
                            <Box >
                              <Checkbox
                                checked={rememberMe}
                                color="primary"
                                className={classes.rememberMeCheck}
                                onClick={handleRememberMe}
                              />Remember Me
                            </Box>

                            <Button variant="text" style={{textTransform:"none"}} onClick={() => setFormLocation(formLocations.forgotPass)}>Forgot Password?</Button>

                            </Grid>
                          </Box>
 
                          <Box mt={mtValue}>
                            <Button disabled={isSubmitting} type="submit" variant="contained" color="primary">Sign In</Button>
                          </Box>
                          
                        </form>

                      )}

                  </Formik>
                </Card>
                <Grid container justify="center">
                <Typography variant="body2" className={classes.alternateOptionText}>New To Product?</Typography>
                <Button variant="text" className={classes.alternateOptionButton} target="_blank" href="https://oncierre.com/get-started/" >Request a Demo</Button>
                </Grid>
              </Grid>
            </Fade>

          }
          {formLocation === formLocations.forgotPass &&
            <Fade in={true}>
              <Grid item xs={12} sm={12} md={12} lg={12}>
                <Card className={classes.formCard} raised={true} onClick={removeForgotPassEmailError}>
                  <img src={Logo}></img>
                  <Typography style={{fontSize:'15px',fontWeight:600,paddingBottom:20}}>Care Solution for Care Providers</Typography>
                <Box >
                  <Formik
                    initialValues={{
                      email: ''
                    }}
                    validationSchema={Yup.object().shape({
                      email: Yup.string().email('Must be a valid email.').max(255).required('Email is required.')
                    })}
                    onSubmit={(values) => {
                      props.forgotPass(values.email)
                      setExpiredOtpTimer()
                      setForgotPassEmail(values.email)
                    }}
                  >
                    {({
                      errors,
                      handleBlur,
                      handleChange,
                      handleSubmit,
                      touched,
                      values
                    }) => (
                        <form
                          noValidate
                          onSubmit={handleSubmit}
                          className={classes.formStyle}
                        >

                            <TextField 
                              name="email" 
                              type="input" 
                              placeholder="Email ID" 
                              variant="outlined" 
                              error={Boolean(touched.email && errors.email)}
                              onChange={handleChange}
                              onBlur={handleBlur}
                              style={{fontSize:'16px'}}
                              //autoFocus
                              helperText={touched.email && errors.email}
                              InputProps={{
                                classes: {
                                  input: classes.textfieldResize,
                                },  
                                startAdornment: (
                                  <InputAdornment position="start">
                                    <Mail size="20"/>
                                  </InputAdornment>
                                ),
                              }}
                              fullWidth
                              />  
                          {props.loginReducer.forgotPassEmailError &&
                            <Typography className={classes.errorAfterApiCall}>Please enter a valid e-mail id.</Typography>
                          }               
                          <Box mt={2}>
                            <Button
                              color="primary"
                              // disabled={(this.props.persistentAlert !== '' && this.props.persistentAlert.message === 'Sending code...') || Boolean(touched.email && errors.email)}
                              fullWidth
                              size="large"
                              type="submit"
                              variant="contained"
                            >
                              Send Code
                          </Button>
                          </Box>
                          <Box mt={2} className={classes.backButtonContainer}> 
                            <Button
                              size="small"
                              variant="small"
                              onClick={() => setFormLocation(formLocations.login)}
                              className={classes.backButton}                            
                            >
                              Cancel
                          </Button>
                          </Box>
                        </form>
                      )}
                  </Formik>
                </Box>
                </Card>
              </Grid>
              </Fade>
          }
          {formLocation === formLocations.changePass &&
            <Fade in={true}>
              <Grid item xs={12} sm={12} md={12} lg={12}>
                <Card className={classes.formCard} raised={true} onClick={() => setInvalidOtp(false)}>
                <img src={Logo}></img>
                <Typography style={{fontSize:'15px',fontWeight:600,paddingBottom:20}}>Care Solution for Care Providers</Typography>
                <Box className={classes.formContainer}>
                  <Formik
                    initialValues={{
                      code: '',
                      password: '',
                      confirm: ''
                    }}
                    validationSchema={Yup.object().shape({
                      code: Yup.number().integer().required('Code is a 6 digit integer.'),
                      password: Yup.string().max(255).required('Password is required.'),
                      confirm: Yup.string().oneOf([Yup.ref('password'), null], 'Passwords must match.').required('Please confirm password.')
                    })}
                    onSubmit={(values) => {
                      props.changePass(values.code, values.password, props.loginReducer.forgotPassToken, forgotPassEmail)
                      .then(resp => {
                        console.log("Getting n deciding",resp)
                        if(resp.success){
                          setFormLocation(formLocations.login)
                        }else{
                          if(resp.message === "Not Valid OTP!"){
                            setInvalidOtp(true)
                          }
                        }
                      },err => console.log("Erro",err))
                    }}
                  >
                    {({
                      errors,
                      handleBlur,
                      handleChange,
                      handleSubmit,
                      touched,
                      values
                    }) => (
                        <form
                          noValidate
                          onSubmit={handleSubmit}
                          className={classes.formStyle}
                        >
                          <TextField
                            error={Boolean(touched.code && errors.code)}
                            fullWidth
                            //autoFocus
                            helperText={touched.code && errors.code}
                            placeholder="Code"
                            name="code"
                            type="number"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            variant="outlined"
                            InputProps={{
                              classes: {
                                input: classes.textfieldResize,
                              },  
                            }}
                            style={{fontSize:'16px'}}

                          />
                          {invalidOtp &&
                            <Typography variant="body2" className={classes.errorAfterApiCall}>{  "Verification code entered is not valid."  }</Typography>
                          }
                          {expiredOtp && !invalidOtp &&
                            <Typography variant="body2" className={classes.errorAfterApiCall}>{  "Verification code expried. Click Send Code Again"  }</Typography>
                          }
                          <TextField
                            error={Boolean(touched.password && errors.password)}
                            fullWidth
                            helperText={touched.password && errors.password}
                            placeholder="Password"
                            margin="normal"
                            name="password"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            type={showPassword1 ? "text" : "password" }
                            value={values.password}
                            variant="outlined"
                            autoComplete="new-password"
                            InputProps={{
                              classes: {
                                input: classes.textfieldResize,
                              },  
                              endAdornment: (
                                <InputAdornment position="start">
                                  <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword1}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                  >
                                    {showPassword1 ? <EyeOff /> : <Eye />}
                                  </IconButton>
                                </InputAdornment>
                              ),
                            }}
                            style={{fontSize:'16px'}}
                          />
                          <TextField
                            error={Boolean(touched.confirm && errors.confirm)}
                            fullWidth
                            helperText={touched.confirm && errors.confirm}
                            placeholder="Confirm Password"
                            margin="normal"
                            name="confirm"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            type={showPassword2 ? "text" : "password" }
                            value={values.confirm}
                            variant="outlined"
                            autoComplete="new-password"
                            InputProps={{
                              classes: {
                                input: classes.textfieldResize,
                              },  
                              endAdornment: (
                                <InputAdornment position="start">
                                  <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword2}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                  >
                                    {showPassword2 ? <EyeOff /> : <Eye />}
                                  </IconButton>
                                </InputAdornment>
                              )
                            }}
                            style={{fontSize:'16px'}}
                          />
                          <Box mt={2} >
                            <Button
                              color="primary"
                              disabled={invalidOtp || expiredOtp}
                              fullWidth
                              size="large"
                              type="submit"
                              variant="contained"
                            >
                              Change Password
                          </Button>
                          </Box>
                          <Box mt={2} className={classes.backButtonContainer}>
                            <Button
                              // disabled={(this.props.persistentAlert !== '' && this.props.persistentAlert.message === 'Sending code...') || (this.props.persistentAlert !== '' && this.props.persistentAlert.message === 'Changing password...')}
                              size="small"
                              variant="small"
                              onClick={resendCode}
                              className={classes.backButton}                            
                            >
                              Send Code Again
                          </Button>
                          </Box>
                          <Box mt={2} className={classes.backButtonContainer}>
                            <Button
                              size="small"
                              variant="small"
                              onClick={cancelOperation}
                              className={classes.backButton}                            
                            >
                              Cancel
                          </Button>
                          </Box>
                        </form>
                      )}
                  </Formik>
                </Box>
                </Card>
              </Grid>
              </Fade>
          }


        <Typography variant="body2" style={{position: 'fixed',  left: 0,  bottom: 0,color:'#6d6d6d'}}>version_0.9</Typography>
        </Grid>
      </div>

    );
}

const mapStateToProps = (state) => {
  return{
    ...state
  }
}
const mapDispatchToProps = (dispatch) => ({
  login: (username, password,rememberMe) => {
    dispatch({ type: 'login_request', username })
    appService.login(username, password).then(json => {
        console.log("We hAve : ",json)
        
        if(json.status === 200){
          dispatch({ type: 'login_success' })
          console.log("Login Resp",json)
          if(rememberMe){
            localStorage.setItem('oncierre-user-email', username);
            localStorage.setItem('oncierre-user-password', password);
          }
        }else
        if(json.status === 401 || 404){
          dispatch({ type: 'login_failure', status:json.status })
        }
        else{
          dispatch({ type: 'login_failure',status:json.status })
        }

    }, error => {
      console.log("Werr hAve : ",error)
      dispatch({ type: 'login_failure',status:401 })

    })
  },
  forgotPass: (email) => {
    console.log("FORGOT PASS CALLED WITH ",email )
    dispatch({ type: 'forgot_pass_request', email })
    appService.forgotPass(email).then(json => {
      if(json.success){
        dispatch({ type: 'forgot_pass_success', json })
      }else{
        dispatch({ type: 'forgot_pass_failure', json })
      }
    }, error => {
      dispatch({ type: 'forgot_pass_failure', error })
    })
  },
  removeLoginFailure: () => {
    dispatch({type:'remove_login_failure'})
  },
  removeForgotPassEmailError:() =>{
    dispatch({type:'remove_forgot_pass_email_error'})
  },
  removeForgotPassToken:() =>{
    dispatch({type:'remove_forgot_pass_token'})
  },
  changePass: (code, password, token, email) => {

    dispatch({ type: 'change_pass_request' })
    return appService.changePass(code, password, token, email).then(json => {
      if(json.success){
        dispatch({ type: 'change_pass_success', json })
        return json
      }else{
        dispatch({ type: 'change_pass_failure', json })
        return json
      }
    }, error => {
      dispatch({ type: 'change_pass_failure', error })
      return error
    })
  }
})

export default connect(mapStateToProps,mapDispatchToProps)(LoginForm);