import React, { useState, useEffect } from 'react'
import { Card, CardContent, Typography, makeStyles, CircularProgress } from '@material-ui/core'
import {appService} from '../../../app.service'
import { useSelector } from 'react-redux'
const useStyle = makeStyles(theme=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function BathroomVisits() {
    const classes = useStyle()
    const [loading, setLoading] = useState(true)
    const [averageBathroomVisits,setAverageBathroomVisits]  = useState(0);
    const patient = useSelector(state => state.patientReducer)
    useEffect(()=>{
        setLoading(true)
        if(patient.hubID){
            appService.fetchBathroomData(patient.hubID).then(json=>{
                if(json.success){
                    json.message = json.message.sort((a,b) => (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0)); 
                    // //console.log("formatted barthrom visits",json.message)
                    var bath = json.message.map(item => parseInt(item.count))
                    bath = bath.filter(e=>(e!==0))
                    //console.log(bath)
                    setAverageBathroomVisits(bath.pop())
                    setLoading(false)
                }
            })
        }
    },[])
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    {loading?(<CircularProgress/>):(
                    <div>
                        <Typography className={classes.info} variant="h6">Bathroom Visits</Typography>
                        <Typography variant="h5" className={classes.info}>{(averageBathroomVisits)===null|| (averageBathroomVisits)===undefined ?'Data not found': (averageBathroomVisits)+' /day'}</Typography>
                    </div>
                    )}
                </CardContent>
            </Card>
        </div>
    )
}
