import React, { useState, useEffect } from 'react'
import { Card, CardContent, Typography, makeStyles, CircularProgress } from '@material-ui/core'
import { useSelector } from 'react-redux'
import {appService} from '../../../app.service'
const useStyle = makeStyles(theme=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function Status() {
    const patient = useSelector(state => state.patientReducer)
    const classes = useStyle()
    const [status, setStatus] = useState('')
    const [loading, setLoading] = useState(true)
    useEffect(()=>{
        setLoading(true)
        if(patient.hubID){
            const timeNow = new Date(Date.now())
            const epochNow = Math.floor(0.001*timeNow)
            const epochBefore = Math.floor(0.001*(timeNow.setMinutes(timeNow.getMinutes() -30)))
            appService.motionData(patient.hubID,epochBefore,epochNow)
                .then(json=>{
                    if(json.length!==0){
                        setStatus('At Home')
                        setLoading(false)
                    }else{
                        setStatus('No Motion')
                        setLoading(false)
                    }  
                })
        }
    },[])
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    {loading?(<CircularProgress/>):(
                        <div>
                            <Typography className={classes.info} variant="h6">Status</Typography>
                            <Typography className={classes.info} variant="h5">{status}</Typography>
                        </div>
                    )}
                </CardContent>
            </Card>
        </div>
    )
}
