const initState = {
    currentPage: 'Overview'
}

export function pageReducer(state,action){
    if(typeof state === 'undefined'){
        return initState;
    }

    switch(action.type){
        case 'change_page':
            return{
                ...state,
                currentPage:action.page
            }
        
        case 'logout':{
            return initState
        }
        default:
            return{
                ...state
            }
    }
}