import React from 'react'
import { Card, CardContent, Typography, makeStyles } from '@material-ui/core'
const useStyle = makeStyles(theme=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function FallRisk() {
    const classes = useStyle()
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    <Typography className={classes.info} variant="h6">Fall Risk Factor</Typography>
                    <Typography className={classes.info} variant="h5">Low</Typography>
                </CardContent>
            </Card> 
        </div>
    )
}
