import React from 'react'
import { Typography, CardContent, Card, Box, Grid, makeStyles } from '@material-ui/core'
import { useState } from 'react'
import Rudrappa from '../../Images/rudrappa.png'
import { useEffect } from 'react';
import {appService} from '../../app.service'
import { useSelector } from 'react-redux';
import LastContact from './Activities/LastContact'
import Location from './Activities/Location';
import ActiveTime from './Activities/ActiveTime';
import Status from './Activities/Status';
import SleepTime from './Activities/SleepTime';
import BathroomVisits from './Activities/BathroomVisits';
import FallRisk from './Activities/FallRisk';
import Profile from './Activities/Profile';
const useStyle = makeStyles((theme) => ({
    
    infoContainer:{
        margin:'1rem',
        // border:'red solid 1rem',
        marginTop:'2rem',
        display:'flex',
        flexWrap:'wrap',
        height:'20rem',
        // alignItems:'center',
        // justifyContent:'space-between'
    },
    tileFlex:{
        display:'flex',
        flexWrap:'wrap'
    }

}))
export default function ProfileData() {
    const classes = useStyle()
       
    return (
        <div className={classes.infoContainer}>
            <Grid className={classes.tileFlex} container spacing={2}>
                <Grid item xs={4}>
                    <Box borderRadius='2rem' boxShadow={10}>
                        <Profile/>
                    </Box>
                </Grid>
                <Grid item xs={8}>
                    <Grid container justify="space-between" spacing={3}>
                        <Grid item xs = {3}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <ActiveTime/>
                        </Box>
                    </Grid>
                    <Grid item xs={3}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <Status/>
                        </Box>
                    </Grid>
                    <Grid item xs={3}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <SleepTime/>
                        </Box>
                    </Grid>
                    <Grid item xs={3}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <Location/>
                        </Box>
                    </Grid>
                    <Grid item xs={4}>
                        <Box borderRadius='1rem' boxShadow={4}>
                            <BathroomVisits/>
                        </Box>
                    </Grid>
                    <Grid item xs={4}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <LastContact/>
                        </Box>
                    </Grid>
                    <Grid item xs={4}>
                        <Box borderRadius='1rem' boxShadow={3}>
                            <FallRisk/>
                        </Box>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    )
}
