import React,{useState,useEffect} from 'react';
import { Grid, Card, makeStyles, Typography, Paper, Badge } from '@material-ui/core';
import {appService} from '../../app.service';
import { connect } from 'react-redux';

import MotionDetectorImage from '../../Images/Motion.png'
import DoorSensorImage from '../../Images/Door.png'
import BedSensorImage from '../../Images/Bed.png'
import io from "socket.io-client";

const useStyle = makeStyles((theme) => ({

    root:{
        paddingLeft:20,
        paddingRight:20,
        height:'100%',
        width:'100%',
        textAlign:'center'
    },
    personCard:{
        maxHeight:260,
        minHeight:260,
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(3),
        marginTop: theme.spacing(3) ,
        margin: 5,
        borderRadius:10,
        paddingTop:10,
        paddingBottom:10
    },
    image:{
        height:'20%',
        width:'25%',
        margin:'auto',
        display:'flex'
    },
    image2:{
        height:'20%',
        width:'50%',
        margin:'auto',
        display:'flex'
    },
    badgeRed:{
        borderRadius:'26px',
        background:'red',

        height:'40px',
        width:'40px',
        top:'25px',
        right:'20px',

        [theme.breakpoints.down('md')]:{
            height:'40px',
            width:'40px',
            top:'25px',
            right:'20px',
        },
        [theme.breakpoints.down('sm')]:{
            height:'15px',
            width:'15px',
            minWidth:'15px',
            top:'10px',
            right:'6px',
        },

    },
    badgeGreen:{
        borderRadius:'26px',
        background:'green',

        height:'40px',
        width:'40px',
        top:'25px',
        right:'20px',

        [theme.breakpoints.down('md')]:{
            height:'40px',
            width:'40px',
            top:'25px',
            right:'20px',
        },
        [theme.breakpoints.down('sm')]:{
            height:'15px',
            width:'15px',
            minWidth:'15px',
            top:'10px',
            right:'6px',
        },

    },
    paperStyle:{
        marginTop:theme.spacing(2),

    }
}))
const SettingsPage = (props) => {

    const classes = useStyle()
    const socket = io('https://oncierreapi.azurewebsites.net');

    const [, updateState] = React.useState();
    const forceUpdate = React.useCallback(() => updateState({}), []);

    const [sensorStatus,setSensorStatus] = useState([
        { area : "Door", status : false, imgResourceNo:3 }, 
        { area : "Bed", status : false, imgResourceNo:2}, 
        { area : "Kitchen", status : false, imgResourceNo:1 }, 
        { area : "Bedroom", status : false, imgResourceNo:1 },
        { area : "Bathroom", status : false, imgResourceNo:1 }, 
    ])

    const [lastContactTs,setLastContactTs] = useState(new Date())

    const [sensors, setSensors] = useState([])

    // const newSensor = (area,status) => {
  
    // }

    // const [doorSensorStatus,setDoorSensorStatus] = useState("Fetching");
    const [systemStatus,setSystemStatus] = useState("Fetching");
    
    let offlineCheckTimer;
        
    const setTimer = () => {
        // console.log("Timer set")
        offlineCheckTimer = setTimeout(() => {
            if(systemStatus === 'Fetching' || systemStatus === 'Online'){
                setSystemStatus("Offline")
            }
        }, 60000);
    }

    // const getLastContact = () => {

    // }

    useEffect(() => {
        appService.lastContact(props.patientReducer.hubID)
        .then(resp =>{
            let d = new Date(0)
            d.setUTCSeconds(resp[0].ts)
            setLastContactTs(d)
            // console.log(d.toString())
        },
        err=> console.log("Resp1:",err))
        appService.currentLocation(props.patientReducer.hubID)
        .then(resp =>{
            console.log("Location is",resp)
        },
        err=> console.log("Resp1:",err))

        setTimer()
        console.log("Listner created")
        if(props.patientReducer.hubID){
            appService.statusMessagesSubscribe(props.patientReducer.hubID)
            .then(json => {
                if(json==="logout"){
                    console.log("Forbidden Access - Logging out")
                    props.logout()
                }
                else{

                        console.log("Subscribed to : ",json.message.room)
                        let tempObj = sensorStatus

                        socket.on(json.message.room, (message) => {
                        console.log("message recived",message)
                            if(message.message){
                                console.log("Recived heartbeat")

                            }else{
                                if(message.ts){
                                    let d = new Date(0)
                                    d.setUTCSeconds(message.ts)
                                    setLastContactTs(d)
                                }
                                if(message.area === "door"){
                                    
                                    if(message.status === "close"){
                                        tempObj.map(item => {
                                            if(item.area === "Door"){
                                                item.status = false
                                            }
                                        })
                                    }else{
                                        tempObj.map(item => {
                                            if(item.area === "Door"){
                                                item.status = true
                                            }
                                        })
                                    }
                                }else
                                if(message.area === "bed"){
                                    
                                    if(message.status === "on-bed"){
                                        tempObj.map(item => {
                                            if(item.area === "Bed"){
                                                item.status = true
                                            }
                                        })
                                    }else{
                                        tempObj.map(item => {
                                            if(item.area === "Bed"){
                                                item.status = false
                                            }
                                        })
                                    }
                                }else
                                if( message.area === "kitchen" ){
                                    
                                    tempObj.map(item => {
                                        if(item.area === "Kitchen"){
                                            item.status = message.motion 
                                        }
                                    })
                                }else    
                                if( message.area === "bedroom" ){
                                    tempObj.map(item => {
                                        if(item.area === "Bedroom"){
                                            item.status = message.motion 
                                        }
                                    })
                                }else
                                if(message.area === "bathroom"){
                                    tempObj.map(item => {
                                        if(item.area === "Bathroom"){
                                            item.status = message.motion 
                                        }
                                    })
                                }
                                else{
                                    console.log("Unknown area ",message.area)
                                }
 
                            }
                            setSystemStatus("Online")
                            console.log("setting with ",tempObj)
                            setSensorStatus(tempObj)
                            forceUpdate()
                            clearTimeout(offlineCheckTimer)
                            setTimer()
                        })
                    }
            }, error => {
            console.log("error",error)
            })

        }else{
            console.log("No HUB ID found for selected user")
        }



        return function cleanup() {
            clearTimeout(offlineCheckTimer)
            if(props.patientReducer.hubID){
                appService.statusMessagesUnSubscribe(props.patientReducer.hubID)
                .then(json => {
                    if(json==="logout"){
                        console.log("Forbidden Access - Logging out")
                        props.logout()
                    }else{
                        
                        console.log("Unsubscribed",json.message.room)
                        // socket.emit("room",json.message.room)
                    }}, error => {
                        console.log("Unsubscrib Error",error)
                    })
            }else{
                console.log("No HUB ID found for selected user")
            }
            

            socket.disconnect()
        }
      
    },[])

    return ( 
        <div className={classes.root}>
            <Paper className={classes.paperStyle}>
            <Grid container   
            direction="row"
            style={{paddingTop:'5px'}}
            >
                <Grid item xs={12} sm={12} md={12} lg={12} xl={12} >
                    
                    <Typography variant="h4" style={{paddingLeft:15,paddingTop:5,paddingBottom:5,textAlign:'center'}}> {systemStatus === 'Fetching' ? '...' : systemStatus.toUpperCase()}</Typography>
                    <Typography variant="body1" style={{paddingLeft:15,paddingTop:5,paddingBottom:5,textAlign:'center'}}> Last seen at : </Typography>
                    <Typography variant="body2" style={{paddingLeft:15,paddingTop:5,paddingBottom:5,textAlign:'center'}}> {lastContactTs.toString()} </Typography>
                </Grid>
    
                {sensorStatus.map(item => {
                    if(item.imgResourceNo === 1){
                        return(
                            <Grid item xs={12} sm={3} md={3} lg={3} xl={3} >

                                <Badge badgeContent={""} color="primary" classes={item.status ? ({colorPrimary:classes.badgeGreen}) : ({colorPrimary:classes.badgeRed})} >
                                <div className={classes.personCard} raised={true} >
                                    <img src={MotionDetectorImage} ></img>


                                <Typography variant="body1" style={{fontWeight:600}}>Motion</Typography>
                                
                                <Grid justify="space-evenly" container style={{paddingTop:10}}>
                                    <Typography variant="body1" style={{fontWeight:400}}>{item.area}</Typography>                   
                                </Grid>
                            
                                </div> 
                                </Badge>
                            </Grid>
                        )
                    }else if(item.imgResourceNo === 2){
                        return(
                            <Grid item xs={12} sm={3} md={3} lg={3} xl={3} >
                                <Badge badgeContent={""} color="primary" classes={item.status ? ({colorPrimary:classes.badgeGreen}) : ({colorPrimary:classes.badgeRed})}  >

                                <div className={classes.personCard} raised={true} >
                                    <img src={BedSensorImage} style={{marginTop:'20px'}}></img>
                                    <Typography variant="body1" style={{fontWeight:600}}>{item.area}</Typography>
                                </div>
                                </Badge>
                            </Grid>

                        )
                    }else{
                        return(
                            <Grid item xs={12} sm={3} md={3} lg={3} xl={3} >
                                <Badge badgeContent={""} color="primary" classes={item.status ? ({colorPrimary:classes.badgeGreen}) : ({colorPrimary:classes.badgeRed})}  >

                                <div className={classes.personCard} raised={true} >
                                    <img src={ DoorSensorImage } ></img>
                                    <Typography variant="body1" style={{fontWeight:600}}>{item.area}</Typography>
                                </div>
                                </Badge>
                            </Grid>

                        )
                    }
                })}
 

            </Grid>
            </Paper>

        </div>

     );
}
 
const mapStateToProps = (state) => {
    return{
        ...state
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        logout: () => {
            dispatch({type:'logout'})
            localStorage.removeItem("oncierre-user-token")    
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(SettingsPage);