const initState = {
    loginStatus:localStorage.getItem('oncierre-user-token') ? true : false,
    loginFailure:false,
    userName:'',
    alert: {
        id: 0,
        type: '',
        message: ''
    },
    persistentAlert: '',
    loginStatusCode: '',
    forgotPassToken:'',
    forgotPassEmailError:''
}

export function loginReducer( state , action) {
    
    if (typeof state === 'undefined') {
        return initState
    }
    switch(action.type){
        case 'login_request':
            return {
                ...state,
                persistentAlert: {
                    type: 'info',
                    message: 'Signing in...'
                },
                loginFailure:false
            }

        case 'login_success':
            return {
                ...state,
                loginStatus: true,
                persistentAlert:''
            }

        case 'login_failure':
            return {
                ...state,
                persistentAlert:'',
                loginFailure:true, 
                loginStatusCode:action.status
            }

        case 'remove_login_failure':
            return {
                ...state,
                persistentAlert:'',
                loginFailure:false, 
                loginStatusCode:''
            }
       
        case 'logout':
            return{
                ...state,
                loginStatus:false
            }

        case 'forgot_pass_request':
            return {
                ...state,
                forgotPassToken: '',
                persistentAlert: {
                    type: 'info',
                    message: 'Sending code...'
                }
            }

        case 'forgot_pass_success':
            return {
                ...state,
                persistentAlert: '',
                forgotPassToken: action.json.message.token,
                alert: {
                    id: state.alert.id + 1,
                    type: 'success',
                    message: 'Email sent with code'
                }
            }

        case 'forgot_pass_failure':
            return {
                ...state,
                persistentAlert: '',
                alert: {
                    id: state.alert.id + 1,
                    type: 'error',
                    message: action.error
                },
                forgotPassEmailError:true
            }

        case 'remove_forgot_pass_email_error':
            return{
                ...state,
                forgotPassEmailError:false
            }

        case 'remove_forgot_pass_token':
            return{
                ...state,
                forgotPassToken:''
            }

        case 'change_pass_request':
            return {
                ...state,
                persistentAlert: {
                    type: 'info',
                    message: 'Changing password...'
                }
            }

        case 'change_pass_success':
            return {
                ...state,
                persistentAlert: '',
                forgotPassToken: '',
                alert: {
                    id: state.alert.id + 1,
                    type: 'success',
                    message: 'Password changed. Please sign in with new password'
                }
            }

        case 'change_pass_failure':
            return {
                ...state,
                persistentAlert: '',
                alert: {
                    id: state.alert.id + 1,
                    type: 'error',
                    message: action.error
                }
                }
            
        default:
            return state
    }
}

