import React from 'react'
import { Card, CardContent, Typography, makeStyles } from '@material-ui/core'
import { useSelector } from 'react-redux'
import NoDp from '../../../Images/noDp.png'
const useStyle =  makeStyles(theme=>({
    infoCard:{
        borderRadius:'1rem',
        justifyContent:'flex-start'
    },
    flexContainer:{
        display:'flex',
        justifyContent:'space-around',
        alignItems:'center',
        flexWrap:'wrap'
    },
    infoImage:{
        display:'inline',
        width:'8rem',
        height:'8rem',
        borderRadius:'50%'
    },
    description:{
        margin:'0.5rem',
        padding:'0.75rem'
    },
}))
export default function Profile() {
    const patient = useSelector(state => state.patientReducer)
    const classes = useStyle()
    let pickImage
    const currentPatient = patient.apartments[patient.selectedPatientIndex]
    if(!currentPatient.image)
        pickImage = NoDp
    else
        pickImage = currentPatient.image
    return (
        <div>
            <Card className={classes.infoCard}>
                <CardContent>
                    <div className={classes.flexContainer}>
                        <img className={classes.infoImage} src={pickImage} alt=""/>
                        <div className={classes.description}>
                            <Typography variant="h6">
                                Room No: {currentPatient.roomNo}
                            </Typography>
                            <Typography variant="h6">
                                Age: {currentPatient.age}
                            </Typography>
                            <Typography style={{textTransform:'capitalize'}} variant="h6">
                                Gender: {currentPatient.gender}
                            </Typography>
                        </div>
                    </div>
                    <div>
                        <Typography style={{textAlign:'center',padding:'1rem'}} variant="h3">
                            {(currentPatient.firstName)===undefined?(currentPatient.name):(`${currentPatient.firstName} ${currentPatient.lastName}`)}
                        </Typography>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}
