import React from 'react';
import {Grid,Card, makeStyles, CardActionArea, Paper} from '@material-ui/core';
import { connect } from 'react-redux';

import BasicPatientInfo from '../PatientData/BasicPatientInfo';
import { useEffect } from 'react';

const useStyle = makeStyles((theme) => ({
        personCard:{
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
        marginTop: '16px' ,
        borderRadius:'10px'
    },
}))

const OverivewPage = (props) => {
    // console.log("Home props",props)
    const classes = useStyle();

    const handlePatientSelect = (index) =>{
        props.selectPatient(index)
        let apartObj = props.patientReducer.apartments.filter(item => item.id === props.patientReducer.apartments[index].id)
        
        if(apartObj.length !== 0){
            props.setHubID(apartObj[0].hubID)
        }
        props.changePage("Profile")
    }
    console.log(props.patientReducer.apartments)
    useEffect(() => {
        const companies = [...new Set(props.patientReducer.apartments.map(({companyID})=>companyID))]
        const centers = [...new Set(props.patientReducer.apartments.map(({centerID})=>centerID))]
        const apartments = [...new Set(props.patientReducer.apartments.map(({apartmentID})=>apartmentID))]
        props.setApartments(apartments)
        props.setCenters(centers)
        props.setCompanies(companies)
        console.log(companies,centers,apartments)
        
    }, [props.patientReducer.apartments])
    return ( 
        <Grid container style={{height:'100vh'}}   alignItems="stretch">
            <Paper style={{width:'100%',margin:'20px',paddingTop:10}}>
                <Grid container >
                    {props.patientReducer.apartments
                        .sort((a,b)=>{
                            if(a.name<b.name) return -1
                            else if(a.name>b.name) return 1
                            return 0
                        })  
                        .map((item,key) => (
                        <Grid item xs={3} sm={3} md={3}lg ={3} key={key} >
                            <CardActionArea onClick={() => handlePatientSelect(key)}>
                                <BasicPatientInfo image={item.image} name={item.name} hubID={item.hubID} noOfPatients={props.patientReducer.apartments.length} />
                            </CardActionArea>
                        </Grid>
                    ))}
                </Grid>  
            </Paper>

            {/* {props.patientReducer.apartments.map((item,key) => (
                <Grid item xs={12} sm={6} md={props.patientReducer.apartments.length === 1 ? 5 : 3} lg={props.patientReducer.apartments.length === 1 ? 5 : 3} xl={props.patientReducer.apartments.length === 1 ? 5 : 3} key={key} >
                    <Card className={classes.personCard} raised={true} onClick={() => handlePatientSelect(key)}>
                        <CardActionArea>
                            <BasicPatientInfo name={item.name} roomNo={item.roomNo} noOfPatients={props.patientReducer.apartments.length}/>
                        </CardActionArea>
                    </Card>
                </Grid>
            ))} */}
    
        </Grid>
     );
}
 
const mapStateToProps = (state) => {
    return{
        ...state
    }
}

const mapDispatchToProps = (dispatch) => ({
    selectPatient: (index) => {
        dispatch({type:'select_patient',index:index})
    },
    setHubID: (hubID) => {
        dispatch({type:'select_hubID',hubID:hubID})
    },
    setApartments: (apartments) => {
        dispatch({type:'set_apartments',apartments:apartments})
    },
    setCompanies: (companies) => {
        dispatch({type:'set_companies',companies:companies})
    },
    setCenters: (centers) => {
        dispatch({type:'set_centers',centers:centers})
    },
    changePage: (page) => {
        dispatch({type:'change_page',page:page})
      }
})
export default connect(mapStateToProps,mapDispatchToProps)(OverivewPage);