import React from 'react'
import PropTypes from 'prop-types';
import { Box, Typography, makeStyles, AppBar, Tabs, Tab } from '@material-ui/core';
import BathroomUsageGraph from '../Graph/BathroomUsageGraph';
import SleepReportGraph from '../Graph/SleepReportGraph';
import MotionDataGraph from '../Graph/MotionDataGraph';
function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },
    tabBar:{
        background:'#FFFFFF',
        color:'#172B4D'
    }
  }));
export default function ChartsData() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
  
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    return (
      <div className={classes.root}>
        <AppBar className={classes.tabBar} position="static">
          <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
          <Tab label="Motion Data" {...a11yProps(0)} />
            <Tab label="BathRoom Usage" {...a11yProps(1)} />
            <Tab label="Sleep Report" {...a11yProps(2)} />
            
          </Tabs>
        </AppBar>
        <TabPanel value={value} index={1}>
          <BathroomUsageGraph/>
        </TabPanel>
        <TabPanel value={value} index={2}>
          <SleepReportGraph/>
        </TabPanel>
        <TabPanel value={value} index={0}>
          <MotionDataGraph/>
        </TabPanel>
      </div>
    );
}
