import React from 'react';
import { Grid, Avatar, Typography,Badge } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles'

import Rudrappa from '../../Images/rudrappa.png';
import Vatsala from '../../Images/vatsala.png';
import Jothi from '../../Images/jothi.png';
import NoDp from '../../Images/noDp.png';
const useStyle = makeStyles((theme) => ({ 

    itemRoot:{
        // minHeight:350,
        paddingTop:20,
        paddingBottom:20,
        textAlign:'center',
    },
    avatar:{
        height:'180px',
        width:'180px',
        margin:'auto',
        [theme.breakpoints.down('md')]:{
            height:'90px',
            width:'90px',
        },
        [theme.breakpoints.down('sm')]:{
            height:'60px',
            width:'60px',
        }
    },
    badge:{
        borderRadius:'26px',
        background:'red',

        height:'40px',
        width:'40px',
        top:'25px',
        right:'20px',

        [theme.breakpoints.down('md')]:{
            height:'40px',
            width:'40px',
            top:'25px',
            right:'20px',
        },
        [theme.breakpoints.down('sm')]:{
            height:'15px',
            width:'15px',
            minWidth:'15px',
            top:'10px',
            right:'6px',
        },

    }
    // name:{
    //     fontSize:'28px',
    //     fontWeight:700,
    //     paddingTop:15,
    //     [theme.breakpoints.down('md')]:{
    //         fontSize:'30px'
    //     }
    // },
    // roomNo:{
    //     fontSize:'20px',
    //     fontWeight:700,
    //     paddingTop:10,
    //     [theme.breakpoints.down('md')]:{
    //         fontSize:'20px'
    //     }
    // },
    
    
}))

const BasicPatientInfo = (props) => {

    const classes= useStyle();


    let pickDp;

    // if(props.name === "Rudrappa Byregowda")
    //     pickDp=Rudrappa
    // else if(props.name === "Jothi Sarojam")
    //     pickDp=Jothi
    // else if(props.name === "Vatsala Rao")
    //     pickDp=Vatsala
    if(!props.image)
        pickDp=NoDp
    else
        pickDp = props.image
    console.log(props)
    
    return ( 
        <div className={props.roomNo === "0" ? classes.root2 : classes.root}>
            <Grid container className={classes.itemRoot} >
                <Grid item xs={12} >
                <Badge badgeContent={""} color="primary" classes={{colorPrimary:classes.badge}} >
                    <Avatar alt="profile pic" src={pickDp} className={classes.avatar}></Avatar>
                </Badge>
                </Grid>
                <Grid item xs={12} >
                    <Typography variant="body1" className={classes.name}>{props.name}</Typography>
                    <Typography variant="body2" className={classes.roomNo}>{props.hubID}</Typography>
                </Grid>
            </Grid>
        </div>
     );
}


export default BasicPatientInfo;