import React from 'react';
import { Badge,Box } from '@material-ui/core'
import { Bell } from 'react-feather'
import { withStyles, makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';

import PatientNotificationsPopUp from '../Components/PatientData/PatientNotificationsPopUp';

const useStyle = makeStyles((theme) => ({
  badgeColor:{
    background:'#f66278'
},

}))

const StyledMenu = withStyles({
    paper: {
      border: '1px solid #d3d4d5',
      width:'70%',
      marginLeft:'auto',
      marginRight:'auto',
      margin:'5%',
      position:'static',
      // marginRight:'10%',
      // marginTop:'1%',
      height:'100%',
      overflow:'visible'
    },
  })((props) => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      // anchorOrigin={{
      //   vertical: 'bottom',
      //   horizontal: 'center',
      // }}
      // transformOrigin={{
      //   vertical: 'top',
      //   horizontal: 'left',
      // }}
      {...props}
      style={{background:'rgba(25,25,25,0.8)'}}
      
    />
  ));


const StyledMenuButton = () => {
    const classes=useStyle()

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
  
    const handleClose = () => {
      setAnchorEl(null);
    };
  

    return ( 
        <Box>
          <IconButton component="span" onClick={handleClick} style={{color:'white',marginLeft:10}}>
            <Badge badgeContent={2} color="primary" classes={{colorPrimary:classes.badgeColor}} >
              <Bell size="26" />
            </Badge>
          </IconButton>

          <StyledMenu
          id="customized-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
          >

          <PatientNotificationsPopUp handleClose={handleClose}/>
          </StyledMenu>
        </Box>
     );
}
 
export default StyledMenuButton;