import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { Grid, FormControl, InputLabel, Select, MenuItem, Button, Typography, makeStyles } from '@material-ui/core';
import {appService} from '../../app.service'
const useStyle = makeStyles((theme) => ({

    formControl: {
        background:'white',
        borderRadius:'1rem',
        margin: theme.spacing(3),
        minWidth: 120,
        width:"12rem",
        height:'4rem'
    },

    alertButton:{
        width:'20rem',
        height:'4rem',
        backgroundColor:"yellow"
    },
    nav:{
        marginLeft:'1rem'
    },
    spacer:{
        flex:'1 1 auto'
    }
    
   

}))
export default function NavigationLayer() {
    const classes = useStyle()
    const patient = useSelector(state => state.patientReducer)
    const [stuckInBathroom,setStuckInBathroom]  = useState('');
    const [company, setCompany] = useState('')
    const [center, setCenter] = useState('')
    const [hubID, setHubID] = useState('')
    const [centers, setCenters] = useState([])
    const [companies, setCompanies] = useState([])
    const [apartments, setApartments] = useState([])
    const dispatch = useDispatch()
    useEffect(()=>{
        if(patient.hubID){
            appService.stuckInBathroom(patient.hubID)
                .then(json=>{
                    console.log(json)
                    if(json.error){
                        setStuckInBathroom('No Events Today')    
                    } else {
                        setStuckInBathroom('Stuck in Bathroom')    
                    }
                })
                .catch(err=>{
                    console.log(err)
                    setStuckInBathroom('No Events for the day')
                })
        }
    })
    const handleChangeCompany = (event) => {
        setCompany(event.target.value);
    };
    const handleChangeCenter = (event) => {
        setCenter(event.target.value);
    };
    const handleChangehubID = (event) => {
        const hubIndex = event.target.value
        console.log(hubIndex)
        setHubID(hubIndex);
    };
    useEffect(() => {
        // dispatch({type:"select_patient",selectedPatientIndex:hubID})
        // let apartObj = patient.apartments.filter(item => item.id === patient.apartments[hubIndex].id)     
        // if(apartObj.length !== 0){
        //     dispatch({type:"select_hubID",hubID:apartObj[0].hubID})
        // }
        // dispatch({type:"change_page",page:"Overview"})
        
    }, [hubID])
    useEffect(() => {
        
        patient.selectedCenters.forEach(centerID=>{
            appService.getCenter(centerID)
                .then(({name})=>(setCenters(prevState=>([...prevState,name]))))
        })
        patient.selectedCompanies.forEach(companyID=>{
            appService.getCompany(companyID)
                .then(({name})=>(setCompanies(prevState=>([...prevState,name]))))
        })
        patient.selectedApartments.forEach(apartmentID=>{
            appService.getAppartment(apartmentID)
                .then(({name})=>(setApartments(prevState=>([...prevState,name]))))
        })

        
    },[])
    return (
        <div>
            <Grid container
                direction="row"
                justify="center"
                alignItems="center" spacing={3} className={classes.nav}>
                    <Grid item >
                    <FormControl variant="filled" className={classes.formControl}>
                                        <InputLabel id="demo-simple-select-outlined-label">Company</InputLabel>
                                        <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={company}
                                        onChange={handleChangeCompany}
                                        label="Age"
                                        >
                                        <MenuItem value="">
                                            <em>None</em>
                                        </MenuItem>
                                        {companies.map((company,idx)=>(<MenuItem value={idx}>{company}</MenuItem>))}
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item>
                                    <FormControl variant="filled" className={classes.formControl}>
                                        <InputLabel id="demo-simple-select-outlined-label">Center</InputLabel>
                                        <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={center}
                                        onChange={handleChangeCenter}
                                        label="Age"
                                        >
                                        <MenuItem value="">
                                            <em>None</em>
                                        </MenuItem>
                                        {centers.map((center,idx)=>(<MenuItem value={idx}>{center}</MenuItem>))}
                                        
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item>
                                    <FormControl variant="filled" className={classes.formControl}>
                                        <InputLabel id="demo-simple-select-outlined-label">Apartment</InputLabel>
                                        <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={hubID}
                                        onChange={handleChangehubID}
                                        label="Age"
                                        >
                                        <MenuItem value="">
                                            <em>None</em>
                                        </MenuItem>
                                        {apartments.map((apartment,idx)=>(<MenuItem value={idx}>{apartment}</MenuItem>))}
                                        
                                        </Select>
                                    </FormControl>
                    </Grid>
                    
                    <Grid item>
                            <Button className={classes.alertButton} variant="contained">
                                <Typography style={{fontWeight:'bold'}} variant="h5">{stuckInBathroom}</Typography>
                            </Button>
                    </Grid>                    
                    
            </Grid>
            
        </div>
    )
}
