function authHeader() {
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.token) {
        return { "Content-Type": "application/json",
            "authorization": `Bearer ${localStorage.getItem('oncierre-user-token')}` 
        };
    } else {
        return {};
    }
}
function login(username, password) {
    const requestOptions = {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        credentials: "omit",
        headers: {
            "Content-Type": "application/json; charset=utf-8"
        },
        body: JSON.stringify({
            "username": username,
            "password": password
        })
    };
    console.log("Here nowww")

    return fetch(`https://oncierreapi.azurewebsites.net/oauth`, requestOptions)
        .then(response => {
            return(response.json().then(data => {
                console.log("response",data)
                if (response.status !== 200) {                    
                    const error = data.message || 'Something went wrong'
                }
                else {
                    if (data.message.token) {
                        localStorage.setItem('oncierre-user-token', data.message.token);
                    }
                }
                return {status:response.status,"new":data.new}
            }))
        })
}

function forgotPass(email) {
    const requestOptions = {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        credentials: "omit",
        headers: {
            "Content-Type": "application/json; charset=utf-8"
        },
        body: JSON.stringify({
            "email": email
        })
    };

    return fetch(`https://oncierreapi.azurewebsites.net/api/forgotpassword`, requestOptions)
        .then(response => {
            return(response.json().then(data => {
                return data
            }))        
        })
}

function changePass(code, password, token, email) {
    const requestOptions = {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        credentials: "omit",
        headers: {
            "Content-Type": "application/json; charset=utf-8"
        },
        body: JSON.stringify({
            email: email,
            secret: code,
            token: token,
            password: password
        })
    };

    return fetch(`https://oncierreapi.azurewebsites.net/api/resetpassword`, requestOptions)
    .then(response => {
        return(response.json().then(data => {
            console.log("responseee2",data)

            return data
        }))        
    })
}

