function getMotionData(id, ts) {
    const token = "Bearer "+localStorage.getItem('oncierre-user-token')
    const requestOptions = {
        method: 'GET',
        mode: 'cors',
        headers: {
            "Content-Type": "application/json; charset=utf-8",
            "authorization": token
        }
    }
    console.log('getting motino data')
    return fetch(`https://oncierreapi.azurewebsites.net/motionData?hubID=${id}&ts=${ts}`, requestOptions)
        .then(handleResponse) 
}
function handleResponse(response) {
    return response.json().then(json => {
        if (!response.ok) {
            if(response.status === 403) {
                // store.dispatch({type:'logout'})
            }
            const error = (json && json.message) || response.statusText;
            return Promise.reject(error);
        } else {
            return json
        }
    });
}