import React, { useState, useEffect } from 'react'
import { makeStyles, Card, CardContent, Typography, CircularProgress } from '@material-ui/core'
import { useSelector } from 'react-redux'
import {appService} from '../../../app.service'
const useStyle = makeStyles((theme)=>({
    info:{
        textTransform:'uppercase',
        fontWeight:'bold',
        textAlign:'center'    
    },
    activityMonitor:{
        padding:'0.75rem',
        height:'8rem',
        borderRadius:'1rem'
    },
}))
export default function LastContact() {
    const classes = useStyle()
    const patient = useSelector(state => state.patientReducer)
    const [lastContact,setLastContact]  = useState('');
    const [loading, setLoading] = useState(true)
    useEffect(()=>{
        if(patient.hubID){
            setLoading(true)
            appService.lastContact(patient.hubID).then(json=>{
                if(json){                    
                    // //console.log("formatted lastContact",json)   
                    
                    const lastCntct = json[0]
                    const dateLast = new Date(lastCntct.ts *1000 )
                    // //console.log(lastCntct.ts,dateLast.toLocaleString('en-IN'),'last Conatact')
                    const formattedDate = dateLast.toLocaleString('en-IN') 
                    
                    setLastContact(formattedDate.slice(0,-6) + " "+formattedDate.substr(-2))
                    setLoading(false)
                }
            })
        }
    },[])
    return (
        <div>
            <Card className={classes.activityMonitor}>
                <CardContent>
                    {loading?(<CircularProgress/>):(
                        <div>
                            <Typography className={classes.info} variant="h6">Last Seen</Typography>
                            <Typography className={classes.info}  variant="h6">{lastContact}</Typography>
                        </div>
                    )}
                </CardContent>
            </Card>
        </div>
    )
}
