import 'date-fns';
import React, { useEffect, Fragment, useState } from 'react'
import { appService } from '../../app.service'
import { useSelector, useDispatch } from 'react-redux'
import bb from 'billboard.js'
import "billboard.js/dist/theme/insight.css"
import { FormControl, InputLabel, Select, MenuItem, makeStyles, TextField, LinearProgress, Grid } from '@material-ui/core'
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';


const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
export default function MotionDataGraph() {
  const patient = useSelector(state => state.patientReducer)
  const [graphEvents, setGraphEvents] = useState([])
  const [loading, setLoading] = useState(true)
  const [interval, setInterval] = useState(6)
  const [interval1, setInterval1] = useState('08-19-2020')
  const [interval2, setInterval2] = useState('08-25-2020')
  const classes = useStyles()
  const dispatch = useDispatch()
  useEffect(() => {
    setLoading(true)
    if(patient.hubID){
      const timeNow = new Date(Date.now())
    const epochNow = Math.floor(0.001*timeNow)
    const epochBefore = Math.floor(0.001*(timeNow.setMinutes(timeNow.getMinutes() -30)))
    appService.getMotionData(patient.hubID,Math.round(Date.now() / 1000) - 3600*interval)
      .then(json=>{
        console.log(json)
        // const graphData = json.map(motion=>({motion.ts,}))
        // setGraphEvents(json)
        // _renderChart(json)
        
      })
      .catch(err=>console.log(err))
    } else {
        dispatch({type:'logout'})
    }
    
  }, [interval])
  useEffect(() => {
    setLoading(true)
    const ts1 = Math.floor(0.001*new Date(interval1))
    const ts2 = Math.floor(0.001*new Date(interval2))
    // console.log(startTime,endTime)
    if(patient.hubID)
      appService.motionData(patient.hubID,ts1,ts2)
      .then(data=>{
        setGraphEvents(data)
          _renderChart(data)
        setLoading(false)
      })
      .catch(err=>console.log(err))
  }, [interval1,interval2])
  const handleChange = (event) => {
    const interval = event.target.value;
    console.log(interval)
    setInterval(interval)
  };
  const handleChange1 = (event) => {
    const interval = event.target.value;
    console.log(interval)
    setInterval1(interval)
  };
  const handleChange2 = (event) => {
    const interval = event.target.value;
    console.log(interval)
    setInterval2(interval)
  };
  const _renderChart = (graphEvents) => {
    
    const motion = graphEvents ? graphEvents.map(e => ({ zone: e.area, x: new Date(e.ts*1000), y: e.motion })) : []
    console.log(motion)
    Array.from(new Set(motion.map(e => e.zone))).filter(z => z !== 'unassigned-sensor' && z !== 'door').map((z, i) => {
      const zone = z.replace(/ /g,'')
      bb.generate({
        size: {
          height: 60
        },
        data: {
          color: function(color, d) { return '#11CDEF' },
          x: 'x',
          columns: [
            ['x', ...motion.filter(e => e.zone === z).map(e => e.x)],
            ['motion', ...motion.filter(e => e.zone === z).map(e => e.y)]
          ],
          type: 'area-step',
          xFormat: "yyyy-MM-dd'T'HH:mm:ssZ"
        },
        line: {
          "step": {
            "type": "step-after"
          }
        },
        zoom: {
          enabled: {
            type: "drag"
          }
        },
        axis: {
          x: {
            type: 'timeseries',
            show: true,
            tick: {
              format: "%m/%d %H:%M",
              fit: false,
              culling: {
                max: 5
              },
            },
            extent: [new Date(Date.now() - (86400000)), new Date(Date.now())]
          },
          y: {
            show: false
          }
        },
        legend: {
          show: false
        },
        tooltip: {
          show: true
        },
        bindto: "#" + zone,
      });
    })


  }
  const [selectedDate, setSelectedDate] = useState(new Date('2014-08-18T21:11:54'));

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };
 
  const motion = graphEvents ? graphEvents.map(e => ({ zone: e.area, x: new Date(e.ts), y: e.motion })) : []
  return (
    <Fragment>
      {loading&&(<LinearProgress />)}
       <Grid container justify="space-around">
          <Grid item>
          <TextField
            id="date"
            label="Start Interval"
            type="date"
            defaultValue="2017-05-24"
            value={interval1}
            onChange={handleChange1}
            className={classes.textField}
            InputLabelProps={{
              shrink: true,
            }}
          />
          </Grid>
          <Grid item>
          <TextField
            id="date"
            label="End Interval"
            type="date"
            defaultValue="2017-05-24"
            value={interval2}
            onChange={handleChange2}
            className={classes.textField}
            InputLabelProps={{
              shrink: true,
            }}
          />
          </Grid>
       </Grid>
      
      
        {motion === [] ? <div style={{display: 'flex', height: '100%', width: '100%', alignItems: 'center', justifyContent: 'center'}}><h1>No events to show</h1></div> : 
        Array.from(new Set(motion.map(e => e.zone))).filter(z => z !== 'unassigned-sensor' && z !== 'door').map((z, i) =>
          <div key={z.replace(/ /g,'')} style={{ padding: '5px', marginBottom: '1%', overflow: 'hidden', width: '100%' }}>
            <div key={z.replace(/ /g,'')}>
              <div style={{ paddingLeft: '10px', marginTop: -10 }}>{z}</div>
              <div id={z.replace(/ /g,'')} style={{marginBottom: -10}}></div>
            </div>
          </div>
        )}
      </Fragment>
  )
}
