import React from 'react';
import PropTypes from 'prop-types';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import logo from '../Images/Logo.png'
import {
  Typography,
  Grid,
  Box,
  Dialog,
  DialogTitle,
  Button,
} from '@material-ui/core'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import {connect} from 'react-redux';

import { makeStyles, useTheme } from '@material-ui/core/styles';
import {
  Home,
  Copy,
  User,
  Settings,
  Menu,
  LogOut,
  BarChart2,
  Clock
} from 'react-feather';

import Dashboard from '../Components/Dasboard';
import {appService} from '../app.service'
const drawerWidth = 266;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    width:'100%'
  },
  drawer: {
    [theme.breakpoints.up('md')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('md')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  // toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    width:'100%'

  },
  overviewText:{
    color:'#7764e4',
  },
  listItemText:{
    fontWeight:700,
    fontSize:'15px'
  },
  paperRoot:{
    width:'900px',
    paddingTop:20,
    paddingBottom:20
    
  },
  logoutButton:{
    color:'#FFFFFF',
    background:'#f88395',
 
  }
}));

const ResponsiveDrawer = (props) => {
  // console.log("Nav props",props)
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [logoutAttempt,setLogoutAttempt] = React.useState(false)

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleOverviewClick = () => {
    props.changePage('Overview') 
    props.unselectPatient() 
  }
  const handleLogoutAttempt = () => {
    if(logoutAttempt)
      setLogoutAttempt(false)
    else
      setLogoutAttempt(true)
  }
  
  const changePage = (page) => {
    props.changePage(page)
  }

  const signOut = (signOutValue) => {
    if(props.pageReducer.currentPage === "System Status"){
      if(signOutValue){
        appService.statusMessagesUnSubscribe(props.patientReducer.hubID)
        .then(json => {
            console.log("Unsubscribed",json.message.room)
            props.logout()
            }, error => {
            console.log("Unsubscrib Error",error)
            })

        
      }else{
        handleLogoutAttempt()
      }
    }
    else{
      if(signOutValue){
        props.logout()
      }else{
        handleLogoutAttempt()
      }
    }
  }

  const drawer = (
    <div>
      <Dialog
        open={logoutAttempt}
        onClose={handleLogoutAttempt}
        style={{textAlign:'center'}}
        classes={{paper:classes.paperRoot}}>
        <DialogTitle id="alert-dialog-title">{"Are you sure you want to logout?"}</DialogTitle>
        {/* <DialogContent>
            <DialogContentText id="alert-dialog-description">
                Are you sure you want to logout?
            </DialogContentText>
        </DialogContent> */}
        <Grid container justify="space-evenly" style={{paddingBottom:30}}>
        {/* <DialogActions style={{paddingBottom:30}}> */}

          <Button onClick={() => signOut(false)} color="primary" variant="contained" style={{width:"150px"}}>
            Cancel
          </Button>
          <Button onClick={() => signOut(true)} color="secondary" variant="contained" style={{width:"150px"}} classes={{containedSecondary:classes.logoutButton}} autoFocus>
            Logout
          </Button>
        {/* </DialogActions> */}
        </Grid>
      </Dialog>


      <Grid 
        container
        direction="row"
        justify="space-between"
        alignItems="center"
        style={{paddingRight:14,paddingLeft:10,paddingTop:10}}>

      
      </Grid>
      {/* <Divider /> */}
      <List style={{paddingLeft:15,paddingRight:15}}>
        {/* {['Home','Trends','Compare','Notes','Task'].map((text, index) => ( */}
        <Grid container direction="column" justify="space-between" style={{height:'90vh'}} >


          <Box>
            <ListItem><img src={logo} alt=""/></ListItem>
          <ListItem button selected={props.pageReducer.currentPage==="Overview"} onClick={handleOverviewClick}>
          <ListItemIcon><Home color="#7764e4" size="27"/></ListItemIcon>
          <ListItemText
           primary={'Overview'} 
           classes={{
             root: classes.overviewText,
             primary: classes.listItemText
            }}/>
        </ListItem>

        { props.patientReducer.selectedPatientIndex !== null &&
            <div>
              <ListItem button selected={props.pageReducer.currentPage==="Profile"} onClick={() => changePage('Profile')}>
                <ListItemIcon><User color="#f88395" size="27"/></ListItemIcon>
                <ListItemText primary={props.patientReducer.apartments[ props.patientReducer.selectedPatientIndex ].name.split(' ').slice(0,1)} classes={{primary:classes.listItemText}}/>
              </ListItem>

              {/* <ListItem button selected={props.pageReducer.currentPage==="Sleep Pattern"} onClick={() => changePage('Sleep Pattern')} >
                <ListItemIcon><Copy color="#7de191" size="27"/></ListItemIcon>
                <ListItemText primary={'Sleep Pattern'} classes={{primary:classes.listItemText}}/>
              </ListItem>
              
              <ListItem button selected={props.pageReducer.currentPage==="Bathroom Usage"} onClick={() => changePage('Bathroom Usage')}>
                <ListItemIcon><Copy color="#fed5a5" size="27"/></ListItemIcon>
                <ListItemText primary={'Bathroom Usage'} classes={{primary:classes.listItemText}}/>
              </ListItem> */}
              
              <ListItem button selected={props.pageReducer.currentPage==="System Status"} onClick={() => changePage('System Status')} >
                <ListItemIcon><Clock color="#6a768c" size="27"/></ListItemIcon>
                <ListItemText primary={'All Notifications'} classes={{primary:classes.listItemText}} />
              </ListItem>
            </div>

        }
          </Box>

          <Box>
            
          <ListItem button onClick={handleLogoutAttempt} >
          <ListItemIcon><LogOut color="#6a768c" size="27" style={{transform:'scaleX(-1)'}} /></ListItemIcon>
          <ListItemText primary={'Logout'} classes={{primary:classes.listItemText}} />
        </ListItem>
          </Box>

        </Grid>
        

        
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      {/* <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            Responsive drawer
          </Typography>
        </Toolbar>
      </AppBar> */}
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden mdUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden smDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <Dashboard toggleDrawer={handleDrawerToggle}  />
      </main>
    </div>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

const mapStateToProps = (state) => {
  return{
    ...state
  }
}

const mapDispatchToProps = (dispatch) => {
  return{
    changePage: (page) => {
      dispatch({type:'change_page',page:page})
    },
    unselectPatient: () => {
      dispatch({type:'unselect_patient'})
    },
    logout: () => {
      dispatch({type:'logout'})
      localStorage.removeItem("oncierre-user-token")
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(ResponsiveDrawer);