import React, { useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Grid,Card, FormControl, InputLabel, Select, MenuItem, Button, Typography } from '@material-ui/core';

import ProfileData from './ProfileData';
import ChartsData from './ChartsData';
import NavigationLayer from './NavigationLayer';

const useStyle = makeStyles((theme) => ({
    root:{

    }
}))
const ProfilePage = () => {
    
    
    const classes = useStyle()
    
    
    
    return ( 
        <div>
            <Grid 
            container 
            direction="column"
            justify="center"
            alignItems="center"
            >
                <Grid item>
                    <NavigationLayer/>
                </Grid>
                <Grid item>
                    <ProfileData/>
                </Grid>
                <Grid style={{width:'90%',height:'80%'}} item>
                    <ChartsData/>
                </Grid>
            </Grid>
        </div>
    );
}
 
export default ProfilePage;