import React from 'react';
import { Typography, Grid, Box,Button, Divider, Card ,IconButton} from '@material-ui/core';
import {makeStyles} from '@material-ui/styles';
import { X,Bell } from 'react-feather';

const useStyles = makeStyles((theme) => ({
    root:{
        textAlign:'center',
        padding:10,
        height:'100%',
        width:'100%',
    },
    notificationContainer:{
        padding:25,
        
        width:'100%',
        background:'#F1F3F9',
        marginLeft:40,
        marginRight:40,
        marginTop:20,
        width:'auto',
        borderRadius:20,

    }
}))
//f5
const PatientNotifications = (props) => {
    const classes=useStyles();
    return ( 
        <div className={classes.root}>
            <Grid container justify="space-between" align="center">
                <Box></Box>
                <Typography style={{fontSize:'23px',fontWeight:800}}>Notifications</Typography>
                <IconButton onClick={props.handleClose}>
                    <X/>
                    <Bell size="34" style={{position:'absolute',top:'-80px',right:'30px',color:'white',border:'4px solid white',borderRadius:35,padding:5,height:'50px',width:'50px' }} />

                </IconButton>
            </Grid>
            <Card className={classes.notificationContainer} raised={true}>
                <Grid container justify="space-between" alignItems="center"  >
                    
                    <Box style={{textAlign:'left'}}>
                        <Typography style={{textTransform:'capitalize' ,fontSize:15}}>
                            Rudrappa Bhadraiah
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Room No. 321
                        </Typography>
                    </Box>
                    <Box style={{textAlign:'center'}}>
                        <Typography style={{textTransform:'uppercase' ,fontSize:15,fontWeight:700}}>
                            Stuck In Bathroom
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Created at 11:30 am
                        </Typography>
                    </Box>
                    <Button variant='contained' color="primary" style={{textTransform:'uppercase',height:45,fontSize:15,width:160}}>
                        Dismiss
                    </Button>
                </Grid>
            </Card>
            
            <Card className={classes.notificationContainer} raised={true}>    
                <Grid container justify="space-between" alignItems="center"  >
                    <Box style={{textAlign:'left'}}>
                        <Typography style={{textTransform:'capitalize' ,fontSize:15}}>
                            Rudrappa Bhadraiah
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Room No. 321
                        </Typography>
                    </Box>
                    <Box style={{textAlign:'center'}}>
                        <Typography style={{textTransform:'uppercase',fontSize:15,fontWeight:700}}>
                            Over-Sleeping
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Created at 9:00 am
                        </Typography>
                    </Box>
                    <Button variant='contained' color="primary" style={{textTransform:'uppercase',height:45,fontSize:15,width:160}}>
                        Dismiss
                    </Button>
                </Grid>
            </Card>
            <Divider style={{marginTop:25, marginLeft:'auto', marginRight:'auto',height:2,color:"#B1BCC9",width:'60%',textAlign:'center'}}/>

            
            <Card className={classes.notificationContainer} raised={true}>
                <Grid container justify="space-between" alignItems="center" >
                    <Box style={{textAlign:'left'}}>
                        <Typography style={{textTransform:'capitalize' ,fontSize:15}}>
                            Rudrappa Bhadraiah
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Room No. 321
                        </Typography>
                    </Box>
                    <Box style={{textAlign:'center'}}>
                        <Typography style={{textTransform:'uppercase',fontSize:15,fontWeight:700}}>
                            Irregular Medication
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Created at 8:00 am
                        </Typography>
                    </Box>
                    <Button variant='contained' color="primary" disabled={true} style={{textTransform:'uppercase',height:45,fontSize:15,width:160}}>
                        Undo
                    </Button>
                </Grid>
            </Card>
            <Card className={classes.notificationContainer} raised={true}>
                <Grid container justify="space-between" alignItems="center" >
                    <Box style={{textAlign:'left'}}>
                        <Typography style={{textTransform:'capitalize' ,fontSize:15}}>
                            Rudrappa Bhadraiah
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Room No. 321
                        </Typography>
                    </Box>
                    <Box style={{textAlign:'center'}}>
                        <Typography style={{textTransform:'uppercase',fontSize:15,fontWeight:700}}>
                            Irregular Medication
                        </Typography>
                        <Typography style={{fontSize:15}}>
                            Created at 8:00 am
                        </Typography>
                    </Box>
                    <Button variant='contained' color="primary" disabled={true} style={{textTransform:'uppercase',height:45,fontSize:15,width:160}}>
                        Undo
                    </Button>
                </Grid>
            </Card>

        </div>
     );
}
 
export default PatientNotifications;