const initState = {
    selectedPatientIndex:null,
    patients:[],
    apartments:[],
    selectedCompanies:[],
    selectedCenters:[],
    selectedApartments:[],
    hubID:null,
}

export function patientReducer(state,action){
    if(typeof state === 'undefined'){
        return initState
    }

    switch(action.type){
        case 'set_patient_list':
            return{
                ...state,
                patients:action.patientList,
            }
        case 'set_apartment_list':{
            return{
                ...state,
                apartments:action.apartmentList
            }
        }
        case 'select_patient':
            return{
                ...state,
                selectedPatientIndex:action.index
            }
        case 'unselect_patient':
            return{
                ...state,
                selectedPatientIndex:null,
                hubID:null
            }
        case 'select_hubID':
            return{
                ...state,
                hubID:action.hubID
            }
        case 'logout':
            return{
                ...initState
            }
        case 'set_apartments':
            return {
                ...state,
                selectedApartments:action.apartments
            }
        case 'set_companies':
            return {
                ...state,
                selectedCompanies:action.companies
            }   
        case 'set_centers':
            return {
                ...state,
                selectedCenters:action.centers
            }    
        default:
            return{
                ...state
            }
    }
} 