import React,{ useEffect,useState } from 'react';
import { Bar,Chart } from 'react-chartjs-2';
import { Card, makeStyles } from '@material-ui/core';
import { connect } from 'react-redux';

import {appService} from '../../app.service';
const useStyle = makeStyles((theme) =>({
}))
const monthsOfYear = [
  'Jan',
  'Feb',
  'Mar',
  'Apr',
  'May',
  'Jun',
  'Jul',
  'Aug',
  'Sep',
  'Oct',
  'Nov',
  'Dec'
]

const horizonalLinePlugin = {
  afterDraw: function(chartInstance) {
      var yScale = chartInstance.scales["y-axis-0"];
      var canvas = chartInstance.chart;
      var ctx = canvas.ctx;
      var index, line, style, width;
      if (chartInstance.options.horizontalLine) {
          for (index = 0; index < chartInstance.options.horizontalLine.length; index++) {
              line = chartInstance.options.horizontalLine[index];
              style = (line.style) ? line.style : "rgba(169,169,169, .6)";
              let yValue = (line.y) ? yScale.getPixelForValue(line.y) : 0 ;
              ctx.lineWidth = (line.width) ? line.width : 3;              
              if (yValue) {
                  ctx.beginPath();
                  ctx.moveTo(chartInstance.chartArea.left, yValue);
                  ctx.lineTo(canvas.width, yValue);
                  ctx.strokeStyle = style;
                  ctx.stroke();
              }
              if (line.text) {
                  ctx.fillStyle = style;
                  ctx.fillText(line.text, 0, yValue + ctx.lineWidth);
              }
          }
          return;
      }
  }
};

Chart.pluginService.register(horizonalLinePlugin);

const SleepReportGraph = (props) => {

  const [dateValues,setDateValues] = useState([])
  const [sleepValues,setSleepValues] = useState([])
  const [averageSleep,setAverageSleep]  = useState(0);
  const [month,setMonth] = useState(0)
  const [year,setYear] = useState(0)

  useEffect(() => {

    if(props.patientReducer.hubID){

      appService.fetchSleepData(props.patientReducer.hubID)
        .then(json => {
          if(json==="logout"){
            console.log("Forbidden Access - Logging out")
            props.logout()
          }
          else if(json.success === true){
            console.log(json)
              json.message = json.message.sort((a,b) => (a.date > b.date) ? 1 : ((b.date > a.date) ? -1 : 0)); 

              var date = json.message.map((item,index) => {
                let dateParts = item.date.split("-")
                if(index === 0){
                  setMonth(dateParts[1])
                  setYear(dateParts[0])
                }
                return monthsOfYear[parseInt(dateParts[1])-1]+" "+(dateParts[2])
              })

              var sleep = json.message.map(item => {
                item = parseFloat(item.duration)
                return item
              })


              setDateValues(date)
              setSleepValues(sleep)
              console.log('sleep avg before removing 0 ',parseFloat((sleep.reduce((pv,cv) => pv+cv,0)/sleep.length).toFixed(2)))
              sleep = sleep.filter(i => parseInt(i) !== 0 ? i : null)
              // console.log("sleep",sleep)
              console.log('sleep avg after removing 0 ',parseFloat((sleep.reduce((pv,cv) => pv+cv,0)/sleep.length).toFixed(2)))
              setAverageSleep(parseFloat((sleep.reduce((pv,cv) => pv+cv,0)/sleep.length).toFixed(2)))
            }
          }, error => {
            console.log("error",error)
          })
    }else{
      console.log("No HUB ID found for selected user")
    }
  },[])
    
    const data = {
      labels: dateValues,
      datasets: [
        {
          label: 'Sleep hours',
          backgroundColor: '#FEC686',
          borderWidth: 1,
          data: sleepValues,
          
        }
      ]
    }

    const options = {
      title:{
      display:true,
      text:'Mean Sleep Time : '+Math.trunc(averageSleep)+' hours '+ (((averageSleep%1)*100).toFixed(0)*0.6).toFixed(0) +' minutes / day',
      fontSize:20,
      },
      legend:{
      display:false,
      position:'right'
      },
      scales: {
          yAxes: [{
              ticks: {
                  beginAtZero:true,
                  min: 0,
                  stepSize:10,
                  max: sleepValues.max    
              }
          }]
      },
      tooltips: {
        callbacks: {
          label: function(tooltipItem, data) {
            var dataset = data.datasets[tooltipItem.datasetIndex];           
            var currentValue = dataset.data[tooltipItem.index];
            return Math.trunc(currentValue) + ' hours ' + (((currentValue%1)*0.6 ).toFixed(2))*100 + ' minutes';
          },
          title: function(tooltipItem, data) {
            return "Sleep Duration : ";
          }
        }
      },
      "horizontalLine": [{
        "y": averageSleep,
        "style" : 'red'
      }]
    }
    const classes = useStyle();
    return (
        <div className={classes.root}>
          <Card style={{
            paddingTop:5,
            paddingBottom:40,
            paddingLeft:60,
            paddingRight:60,
            marginLeft:10,
            borderRadius:10,
            height:"100%",
            width:'95%'
            }}
            >
            <Bar
            height={65}
            data={data}
            options={options}
            
            />
        </Card>
        </div>
        );
  
}

const mapStateToProps = (state) => {
  return{
    ...state
  }
}

const mapDispatchToProps = (dispatch) => {
  return{
    logout: () => {
      dispatch({type:'logout'})
      localStorage.removeItem("oncierre-user-token")    
    }
  }
} 



export default connect(mapStateToProps,mapDispatchToProps)(SleepReportGraph);